.lg-css3 {
  &.lg-soft-zoom {
    .lg-item {
      opacity: 0;

      &.lg-prev-slide {
        @include scale3d(1.1, 1.1, 1.1);
      }

      &.lg-next-slide {
        @include scale3d(0.9, 0.9, 0.9);
      }

      &.lg-current {
        @include scale3d(1, 1, 1);
        opacity: 1;
      }

      &.lg-prev-slide,
      &.lg-next-slide,
      &.lg-current {
        @include transitionCustom(
          transform 1s cubic-bezier(0, 0, 0.25, 1) 0s,
          opacity 1s ease 0s
        );
      }
    }
  }
}
