.lg-css3 {
  &.lg-slide-vertical {
    .lg-item {
      opacity: 0;

      &.lg-prev-slide {
        @include translate3d(0, -100%, 0);
      }

      &.lg-next-slide {
        @include translate3d(0, 100%, 0);
      }

      &.lg-current {
        @include translate3d(0, 0, 0);
        opacity: 1;
      }

      &.lg-prev-slide,
      &.lg-next-slide,
      &.lg-current {
        @include transitionCustom(
          transform 1s cubic-bezier(0, 0, 0.25, 1) 0s,
          opacity 0.1s ease 0s
        );
      }
    }
  }
}
